import React, { useMemo } from 'react';

import {
  Box,
  Button,
  Divider,
  Grid,
  FormControl,
  FormControlLabel,
  RadioGroup,
  Radio,
  Paper,
  TextField,
  Typography,
  TableContainer,
  Table,
  TableHead,
  TableBody,
  TableRow,
  TableCell,
  ToggleButtonGroup,
  ToggleButton,
  InputAdornment,
  Select,
  MenuItem,
  Checkbox,
} from '@mui/material';
import SearchIcon from '@mui/icons-material/Search';
import { ThemeProvider } from '@mui/material/styles';
import { LocalizationProvider } from '@mui/x-date-pickers/LocalizationProvider';
import { AdapterDayjs } from '@mui/x-date-pickers/AdapterDayjs';
import { DesktopDatePicker } from '@mui/x-date-pickers/DesktopDatePicker';

import { searchbuttontheme, initbuttontheme, sx } from './styles';

const ScreenView = ({
  reportList = [],
  checked = [],
  totalReport,
  uncheckedReport,
  searchOption,
  searchKeyword,
  period,
  reportListOption,
  startDate,
  endDate,
  periods = [],
  reportListOptions = [],
  handleSearchOption = () => {},
  handleSearchKeyword = () => {},
  handlePeriod = () => {},
  handleStartDate = () => {},
  handleEndDate = () => {},
  handleSearch = () => {},
  handleInitOptions = () => {},
  handleCheckedDelete = () => {},
  handleCheckedRead = () => {},
  handleReportListOption = () => {},
  handleAllCheck = () => {},
  handleCheck = () => {},
}) => {
  const arrayContainsArray = (superset, subset) => {
    if (subset.length === 0) {
      return 0;
    }

    if (subset.every(value => superset.indexOf(value) >= 0)) {
      return 100;
    }
    if (subset.some(value => superset.indexOf(value) >= 0)) {
      return 50;
    }
    return 0;
  };

  const checkState = useMemo(() => {
    let checkSelection = 0;
    if (reportList && reportList.length > 0 && checked && checked.length > 0) {
      checkSelection = arrayContainsArray(
        checked,
        reportList.map(x => x.id),
      );
    }
    return checkSelection;
  }, [reportList, checked]);

  return (
    <>
      <Grid container sx={sx.grid}>
        <Grid item xs={12}>
          <Paper sx={sx.paper}>
            <Typography sx={sx.typotitle}> </Typography>
            <Divider />
            <Grid container spacing={1}>
              <Grid item xs={2}>
                <Typography sx={sx.typoschedule}> </Typography>
              </Grid>
              <Grid item xs={2}>
                <Typography sx={sx.typoschedule}>  </Typography>
              </Grid>
            </Grid>
            <Grid container spacing={1}>
              <Grid item xs={2}>
                <Typography sx={sx.typoschedulecount}>{totalReport}</Typography>
              </Grid>
              <Grid item xs={2}>
                <Typography sx={sx.typoschedulecount}>
                  {uncheckedReport}
                </Typography>
              </Grid>
            </Grid>
          </Paper>
        </Grid>
      </Grid>
      <Grid container sx={sx.grid}>
        <Grid item xs={12}>
          <Paper sx={sx.paper}>
            <Grid container spacing={1}>
              <Grid item xs={1}>
                <Typography sx={sx.typoschedulesearch}></Typography>
              </Grid>
              <Grid item xs={2}>
                <FormControl>
                  <RadioGroup
                    value={searchOption}
                    onChange={handleSearchOption}
                  >
                    <FormControlLabel
                      value="name"
                      control={<Radio />}
                      label=" "
                    />
                    <FormControlLabel
                      value="nick"
                      control={<Radio />}
                      label=" "
                    />
                  </RadioGroup>
                </FormControl>
              </Grid>
              <Grid item xs={6}>
                <TextField
                  margin="normal"
                  variant="standard"
                  InputProps={{
                    disableUnderline: true,
                    placeholder: ' .',
                    startAdornment: (
                      <InputAdornment position="start">
                        <SearchIcon sx={sx.searchicon} />
                      </InputAdornment>
                    ),
                  }}
                  size="small"
                  sx={sx.search}
                  value={searchKeyword}
                  onChange={handleSearchKeyword}
                />
              </Grid>
            </Grid>
            <Grid container spacing={1}>
              <Grid item xs={1}>
                <Typography sx={sx.typoschedulesearch}></Typography>
              </Grid>
              <Grid item xs={10}>
                <ToggleButtonGroup
                  exclusive
                  value={period}
                  onChange={handlePeriod}
                  sx={sx.togglegroup}
                >
                  {periods.map(e => (
                    <ToggleButton key={e.value} value={e.value} sx={sx.toggle}>
                      {e.title}
                    </ToggleButton>
                  ))}
                </ToggleButtonGroup>
                <LocalizationProvider dateAdapter={AdapterDayjs}>
                  <DesktopDatePicker
                    inputFormat="YYYY.MM.DD"
                    value={startDate}
                    onChange={handleStartDate}
                    renderInput={params => (
                      <TextField
                        {...params}
                        disabled
                        size="small"
                        sx={sx.typocalendartextfield}
                      />
                    )}
                  />
                  <Typography sx={sx.typocalendar}>~</Typography>
                  <DesktopDatePicker
                    inputFormat="YYYY.MM.DD"
                    value={endDate}
                    onChange={handleEndDate}
                    renderInput={params => (
                      <TextField
                        {...params}
                        disabled
                        size="small"
                        sx={sx.typocalendartextfield}
                      />
                    )}
                  />
                </LocalizationProvider>
              </Grid>
            </Grid>
            <Grid container spacing={1}>
              <Grid display="flex" justifyContent="center" item xs={12}>
                <ThemeProvider theme={searchbuttontheme}>
                  <Button
                    sx={sx.button}
                    variant="contained"
                    disabled={false}
                    onClick={handleSearch}
                  >
                    
                  </Button>
                </ThemeProvider>
                <ThemeProvider theme={initbuttontheme}>
                  <Button
                    sx={sx.button}
                    variant="contained"
                    disabled={false}
                    onClick={handleInitOptions}
                  >
                    
                  </Button>
                </ThemeProvider>
              </Grid>
            </Grid>
          </Paper>
        </Grid>
      </Grid>
      <Grid container sx={sx.grid}>
        <Grid item xs={12}>
          <Paper sx={sx.paper}>
            <Typography sx={sx.typotitle}> </Typography>
            <Box sx={sx.tablebuttonbox}>
              <Button
                sx={sx.tablebutton}
                variant="outlined"
                disabled={checked.length === 0}
                onClick={handleCheckedDelete}
              >
                 
              </Button>
              <Button
                sx={sx.tablebutton}
                variant="outlined"
                disabled={checked.length === 0}
                onClick={handleCheckedRead}
              >
                  
              </Button>
              <FormControl size="small">
                <Select
                  displayEmpty
                  value={reportListOption}
                  onChange={handleReportListOption}
                  sx={sx.selectschedule}
                >
                  <MenuItem disabled value="">
                     
                  </MenuItem>
                  {reportListOptions.map(e => (
                    <MenuItem key={e.value} value={e.value}>
                      {e.title}
                    </MenuItem>
                  ))}
                </Select>
              </FormControl>
            </Box>
            <Divider />
            <TableContainer sx={sx.tablecontainer}>
              <Table sx={sx.table} size="small" stickyHeader>
                <TableHead sx={sx.tableheaderrow}>
                  <TableRow>
                    <TableCell align="center" sx={sx.tableheader}>
                      <Checkbox
                        indeterminate={checkState === 50}
                        checked={checkState === 100}
                        onChange={e => handleAllCheck(e)}
                      />
                    </TableCell>
                    <TableCell align="center" sx={sx.tableheader}>
                      
                    </TableCell>
                    <TableCell align="center" sx={sx.tableheader}>
                      
                    </TableCell>
                    <TableCell align="center" sx={sx.tableheader}>
                      
                    </TableCell>
                    <TableCell align="center" sx={sx.tableheader}>
                      
                    </TableCell>
                    <TableCell align="center" sx={sx.tableheader}>
                      
                    </TableCell>
                  </TableRow>
                </TableHead>
                <TableBody>
                  {reportList.length !== 0 &&
                    reportList.map(e => {
                      const isChecked = !!checked.find(n => n === e.id);
                      return (
                        <TableRow>
                          <TableCell align="center" sx={sx.tablebody}>
                            <Checkbox
                              checked={isChecked}
                              onChange={event => handleCheck(event, e.id)}
                            />
                          </TableCell>
                          <TableCell align="center" sx={sx.tablebody}>
                            {e.id}
                          </TableCell>
                          <TableCell align="center" sx={sx.tablebody}>
                            
                          </TableCell>
                          <TableCell align="center" sx={sx.tablebody}>
                             
                          </TableCell>
                          <TableCell align="center" sx={sx.tablebody}>
                             
                          </TableCell>
                          <TableCell align="center" sx={sx.tablebody}>
                            
                          </TableCell>
                        </TableRow>
                      );
                    })}
                </TableBody>
              </Table>
            </TableContainer>
          </Paper>
        </Grid>
      </Grid>
    </>
  );
};

export default ScreenView;
