import React from 'react';
import { useDispatch } from 'react-redux';

import { logout } from '../../store';

import View from './view';

const HomeScreen = () => {
  console.log('HomeScreen');

  const dispatch = useDispatch();

  const sidemenu = [
    { text: '', link: '/dashboard' },
    { text: ' ', link: '/user' },
    { text: ' ', link: '/schedule' },
    { text: ' ', link: '/report' },
  ];

  const handleLogout = () => {
    dispatch(logout());
  };

  return <View sidemenu={sidemenu} handleLogout={handleLogout} />;
};

export default HomeScreen;
