import React from 'react';
import GoogleLogin from 'react-google-login';

import { Button, Box, Container, TextField } from '@mui/material';
import { ThemeProvider } from '@mui/material/styles';

import { Letdy } from '../../assets/images';

import {
  basiclogintheme,
  kakaotheme,
  googletheme,
  sx,
  inputtheme,
} from './styles';
import './styles.css';

const ScreenView = ({
  handleBasicLogin = () => {},
  handleKakaoLogin = () => {},
  handleGoogleLoginSuccess = () => {},
  handleGoogleLoginFailure = () => {},
}) => (
  <div>
    <Container component="main" maxWidth={false}>
      <Box sx={sx.box}>
        <img className="logo" alt="iPhone_01" src={Letdy} />
        <p className="logo_text">ADMIN</p>
        <div className="input">
          <p className="input_text"></p>
          <ThemeProvider theme={inputtheme}>
            <TextField sx={sx.input} id="outlined-basic" variant="outlined" />
          </ThemeProvider>
          <p className="input_text"></p>
          <ThemeProvider theme={inputtheme}>
            <TextField
              sx={sx.input}
              id="outlined-basic"
              variant="outlined"
              type="password"
            />
          </ThemeProvider>
        </div>
        <div className="button">
          <ThemeProvider theme={basiclogintheme}>
            <Button
              sx={sx.button}
              variant="contained"
              disabled={false}
              onClick={handleBasicLogin}
            >
              <p className="login_text"></p>
            </Button>
          </ThemeProvider>
        </div>
        <div className="button">
          <ThemeProvider theme={kakaotheme}>
            <Button
              sx={sx.button}
              variant="contained"
              disabled={false}
              onClick={handleKakaoLogin}
            >
              <p className="login_text"> </p>
            </Button>
          </ThemeProvider>
        </div>
        <div>
          <GoogleLogin
            clientId={process.env.REACT_APP_GOOGLE_CLIENT_ID}
            render={renderProps => (
              <ThemeProvider theme={googletheme}>
                <Button
                  sx={sx.button}
                  variant="contained"
                  disabled={false}
                  onClick={renderProps.onClick}
                >
                  <p className="login_text"> </p>
                </Button>
              </ThemeProvider>
            )}
            buttonText=" "
            onSuccess={handleGoogleLoginSuccess}
            onFailure={handleGoogleLoginFailure}
          />
        </div>
      </Box>
    </Container>
  </div>
);

export default ScreenView;
