import React, { useCallback, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { useSelector, useDispatch } from 'react-redux';
import { useMutation } from 'react-query';

import axiosApi from '../../utils/api';
import { useAlert } from '../../hooks';
import { testlogin, login, fetchUser } from '../../store';

import View from './view';

const LoginScreen = () => {
  const dispatch = useDispatch();
  const { isLoggedIn } = useSelector(state => state.user);
  const navigate = useNavigate();

  const KAKAO_AUTH_URL = `https://kauth.kakao.com/oauth/authorize?client_id=${process.env.REACT_APP_KAKAO_CLIENT_ID}&redirect_uri=${process.env.REACT_APP_KAKAO_REDIRECT_ID}&response_type=code`;

  const { show, hide } = useAlert();

  const { mutateAsync } = useMutation(payload =>
    axiosApi.post('/users', payload),
  );

  const onConfirmAlert = useCallback(() => {
    hide();
  }, [hide]);

  const showErrorMessage = useCallback(
    ({ message = '' }) => {
      show({
        content: message,
        label: '',
        onConfirm: () => onConfirmAlert(),
      });
    },
    [show, onConfirmAlert],
  );

  useEffect(() => {
    if (isLoggedIn) {
      navigate('/');
    }
  }, [isLoggedIn]);

  const handleLogin = useCallback(
    result => {
      dispatch(login(result));
      dispatch(fetchUser());
    },
    [dispatch],
  );

  const handleBasicLogin = () => {
    dispatch(testlogin(true));
  };

  const handleKakaoLogin = () => {
    window.location.href = KAKAO_AUTH_URL;
  };

  const handleGoogleLoginSuccess = async res => {
    const { accessToken } = res;
    console.log(accessToken);

    try {
      if (accessToken) {
        const { data, status } = await mutateAsync({
          data: accessToken,
          identity_provider: 'kakao',
        });
        handleLogin({ data, status });
        navigate('/');
      }
    } catch (error) {
      console.error(error);
      showErrorMessage({ message: '  .' });
    }
  };

  const handleGoogleLoginFailure = error => {
    console.log(error);
    showErrorMessage({ message: '  .' });
  };

  return (
    <View
      handleBasicLogin={handleBasicLogin}
      handleKakaoLogin={handleKakaoLogin}
      handleGoogleLoginSuccess={handleGoogleLoginSuccess}
      handleGoogleLoginFailure={handleGoogleLoginFailure}
    />
  );
};

export default LoginScreen;
