import React, { useCallback, useEffect, useState } from 'react';

import dayjs from 'dayjs';

import { useAlert } from '../../hooks';

import View from './view';

const ReportManageScreen = () => {
  console.log('ReportManageScreen');
  const DAYJS_FORMAT = 'YYYY.MM.DD';

  const { show, hide } = useAlert();

  const [totalReport, setTotalReport] = useState(0);
  const [uncheckedReport, setUncheckedReport] = useState(0);
  const [searchOption, setSearchOption] = useState('name');
  const [searchKeyword, setSearchKeyword] = useState('');
  const [period, setPeriod] = useState('today');
  const [startDate, setStartDate] = useState(dayjs().format(DAYJS_FORMAT));
  const [endDate, setEndDate] = useState(dayjs().format(DAYJS_FORMAT));

  const [checked, setChecked] = useState([]);
  const [reportListOption, setReportListOption] = useState('');

  const [reportList, setReportList] = useState([]);

  const periods = [
    { value: 'today', title: '' },
    { value: 'week', title: '1' },
    { value: 'month', title: '1' },
    { value: 'three_month', title: '3' },
    { value: 'six_month', title: '6' },
    { value: 'year', title: '1' },
    { value: 'all', title: '' },
  ];

  const reportListOptions = [
    { value: 'total', title: ' ' },
    { value: 'read', title: '  ' },
    { value: 'unread', title: '   ' },
  ];

  useEffect(() => {
    setTotalReport(0);
    setUncheckedReport(0);
    setChecked([]);
    setReportList([]);
  }, []);

  const onConfirmAlert = useCallback(() => {
    hide();
  }, [hide]);

  const showErrorMessage = useCallback(
    ({ message = '' }) => {
      show({
        content: message,
        label: '',
        onConfirm: () => onConfirmAlert(),
      });
    },
    [show, onConfirmAlert],
  );

  const handleSearchOption = event => {
    setSearchOption(event.target.value);
  };

  const handleSearchKeyword = event => {
    setSearchKeyword(event.target.value);
  };

  const handlePeriod = (event, newPeriod) => {
    setPeriod(newPeriod);
  };

  const handleStartDate = newDate => {
    if (newDate.isAfter(endDate)) {
      showErrorMessage({ message: '     .' });
    } else {
      setStartDate(newDate);
    }
  };

  const handleEndDate = newDate => {
    if (newDate.isBefore(startDate)) {
      showErrorMessage({ message: '     .' });
    } else {
      setEndDate(newDate);
    }
  };

  const handleSearch = () => {
    console.log('search');
  };

  const handleInitOptions = () => {
    console.log(startDate, endDate);
    setSearchOption('name');
    setSearchKeyword('');
    setPeriod('today');
    setStartDate(dayjs().format(DAYJS_FORMAT));
    setEndDate(dayjs().format(DAYJS_FORMAT));
  };

  const handleCheckedDelete = () => {
    console.log(checked);
  };

  const handleCheckedRead = () => {
    console.log(checked);
    // Request (Read  )
  };

  const handleReportListOption = event => {
    setReportListOption(event.target.value);
  };

  const handleAllCheck = event => {
    if (event.target.checked) {
      setChecked(reportList.map(e => e.id));
    } else {
      setChecked([]);
    }
  };

  const setCheckedId = id => {
    const checkeds = Array.from(checked);
    if (checkeds) {
      const index = checkeds.findIndex(e => e === id);
      if (index > -1) {
        checkeds.splice(index, 1);
        return checkeds;
      }
      checkeds.push(id);
      return checkeds;
    }
    return [id];
  };

  const handleCheck = (event, id) => {
    const newCheckedIds = setCheckedId(id);
    setChecked(newCheckedIds);
  };

  return (
    <View
      reportList={reportList}
      checked={checked}
      totalReport={totalReport}
      uncheckedReport={uncheckedReport}
      searchOption={searchOption}
      searchKeyword={searchKeyword}
      period={period}
      startDate={startDate}
      endDate={endDate}
      reportListOption={reportListOption}
      periods={periods}
      reportListOptions={reportListOptions}
      handleSearchOption={handleSearchOption}
      handleSearchKeyword={handleSearchKeyword}
      handlePeriod={handlePeriod}
      handleStartDate={handleStartDate}
      handleEndDate={handleEndDate}
      handleSearch={handleSearch}
      handleInitOptions={handleInitOptions}
      handleCheckedDelete={handleCheckedDelete}
      handleCheckedRead={handleCheckedRead}
      handleReportListOption={handleReportListOption}
      handleAllCheck={handleAllCheck}
      handleCheck={handleCheck}
    />
  );
};

export default ReportManageScreen;
