import React, { useCallback, useEffect, useState } from 'react';

import dayjs from 'dayjs';

import { useAlert } from '../../hooks';

import View from './view';

const ScheduleManageScreen = () => {
  console.log('ScheduleManageScreen');
  const DAYJS_FORMAT = 'YYYY.MM.DD';

  const { show, hide } = useAlert();

  const [totalSchedule, setTotalSchedule] = useState(0);
  const [closedSchedule, setClosedSchedule] = useState(0);
  const [searchOption, setSearchOption] = useState('host');
  const [searchKeyword, setSearchKeyword] = useState('');
  const [leisure, setLeisure] = useState('');
  const [peopleCount, setPeopleCount] = useState('');
  const [period, setPeriod] = useState('today');
  const [startDate, setStartDate] = useState(dayjs().format(DAYJS_FORMAT));
  const [endDate, setEndDate] = useState(dayjs().format(DAYJS_FORMAT));

  const [checked, setChecked] = useState([]);
  const [scheduleListOption, setScheduleListOption] = useState('');

  const [scheduleList, setScheduleList] = useState([]);

  // axios (/leisure)  
  const leisures = [
    { value: 'golf', title: '' },
    { value: 'fishing', title: '' },
    { value: 'basket', title: '' },
    { value: 'hiking', title: '' },
  ];

  const peopleCounts = [
    { value: '5', title: '1~5' },
    { value: '10', title: '6~10' },
  ];

  const periods = [
    { value: 'today', title: '' },
    { value: 'week', title: '1' },
    { value: 'month', title: '1' },
    { value: 'three_month', title: '3' },
    { value: 'six_month', title: '6' },
    { value: 'year', title: '1' },
    { value: 'all', title: '' },
  ];

  const scheduleListOptions = [
    { value: 'normal', title: '   ' },
    { value: 'recommend', title: '   ' },
  ];

  useEffect(() => {
    setTotalSchedule(0);
    setClosedSchedule(0);
    setChecked([]);
    setScheduleList([]);
  }, []);

  const onConfirmAlert = useCallback(() => {
    hide();
  }, [hide]);

  const showErrorMessage = useCallback(
    ({ message = '' }) => {
      show({
        content: message,
        label: '',
        onConfirm: () => onConfirmAlert(),
      });
    },
    [show, onConfirmAlert],
  );

  const handleSearchOption = event => {
    setSearchOption(event.target.value);
  };

  const handleSearchKeyword = event => {
    setSearchKeyword(event.target.value);
  };

  const handleLeisure = event => {
    setLeisure(event.target.value);
  };

  const handlePeopleCount = event => {
    setPeopleCount(event.target.value);
  };

  const handlePeriod = (event, newPeriod) => {
    setPeriod(newPeriod);
  };

  const handleStartDate = newDate => {
    if (newDate.isAfter(endDate)) {
      showErrorMessage({ message: '     .' });
    } else {
      setStartDate(newDate);
    }
  };

  const handleEndDate = newDate => {
    if (newDate.isBefore(startDate)) {
      showErrorMessage({ message: '     .' });
    } else {
      setEndDate(newDate);
    }
  };

  const handleSearch = () => {
    console.log('search');
  };

  const handleInitOptions = () => {
    console.log(startDate, endDate);
    setSearchOption('host');
    setSearchKeyword('');
    setLeisure('');
    setPeopleCount('');
    setPeriod('today');
    setStartDate(dayjs().format(DAYJS_FORMAT));
    setEndDate(dayjs().format(DAYJS_FORMAT));
  };

  const handleCheckedDelete = () => {
    console.log(checked);
  };

  const handleCheckedRecommend = () => {
    console.log(checked);
  };

  const handleScheduleListOption = event => {
    setScheduleListOption(event.target.value);
  };

  const handleAllCheck = event => {
    if (event.target.checked) {
      setChecked(scheduleList.map(e => e.id));
    } else {
      setChecked([]);
    }
  };

  const setCheckedId = id => {
    const checkeds = Array.from(checked);
    if (checkeds) {
      const index = checkeds.findIndex(e => e === id);
      if (index > -1) {
        checkeds.splice(index, 1);
        return checkeds;
      }
      checkeds.push(id);
      return checkeds;
    }
    return [id];
  };

  const handleCheck = (event, id) => {
    const newCheckedIds = setCheckedId(id);
    setChecked(newCheckedIds);
  };

  return (
    <View
      scheduleList={scheduleList}
      checked={checked}
      totalSchedule={totalSchedule}
      closedSchedule={closedSchedule}
      searchOption={searchOption}
      searchKeyword={searchKeyword}
      leisure={leisure}
      peopleCount={peopleCount}
      period={period}
      startDate={startDate}
      endDate={endDate}
      scheduleListOption={scheduleListOption}
      leisures={leisures}
      peopleCounts={peopleCounts}
      periods={periods}
      scheduleListOptions={scheduleListOptions}
      handleSearchOption={handleSearchOption}
      handleSearchKeyword={handleSearchKeyword}
      handleLeisure={handleLeisure}
      handlePeopleCount={handlePeopleCount}
      handlePeriod={handlePeriod}
      handleStartDate={handleStartDate}
      handleEndDate={handleEndDate}
      handleSearch={handleSearch}
      handleInitOptions={handleInitOptions}
      handleCheckedDelete={handleCheckedDelete}
      handleCheckedRecommend={handleCheckedRecommend}
      handleScheduleListOption={handleScheduleListOption}
      handleAllCheck={handleAllCheck}
      handleCheck={handleCheck}
    />
  );
};

export default ScheduleManageScreen;
